.App {
  text-align: center;
}

h1{
  font-family: 'Bigelow Rules';
  font-size: 72px;
  color: #0ccac4;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.card-list{
    width: 85vw;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 20px;
}
.card-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    background-color:#95dada;
    border: 1px solid grey;
    border-radius: 5px;
    padding: 25px;
    cursor: pointer;
    -moz-osx-font-smoothing:grayscale;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    -webkit-transform: translateZ(0);
            transform: translateZ(0);
    transition: -webkit-transform 0.25s ease-out;
    transition: transform 0.25s ease-out;
    transition: transform 0.25s ease-out, -webkit-transform 0.25s ease-out;
}


.card-container:hover{
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}
.modal{
    position: fixed;
    z-index: 2;
    left: 0;
    top: 0; 
    right: 0; 
    bottom: 0; 
    background-color: rgba(0,0,0,0.7);
    display: -webkit-flex;
    display: flex; 
    -webkit-align-items: center; 
            align-items: center;
    -webkit-justify-content:center;
            justify-content:center;
    font-size: 20px;
     color: #05a1b6fb;
     font-weight: bold;
}

.modal-content{
    width: 500px; 
    background-color: #fff; 
}

.modal-header .modal-footer{
    padding: 10px;
    background-color:#95dada;

}

.modal-title{
    margin:0;
    background-color:#95dada;

}

.modal-body{
    padding: 10px;
    

}


/* Same as card item, minus the hover effect */
.card-modal-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    background-color:#95dada;
    border-radius: 5px;
    padding: 25px;
    cursor: pointer;
    -moz-osx-font-smoothing:grayscale;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    -webkit-transform: translateZ(0);
            transform: translateZ(0);
    transition: -webkit-transform 0.25s ease-out;
    transition: transform 0.25s ease-out;
    transition: transform 0.25s ease-out, -webkit-transform 0.25s ease-out;
}

.dropdown{
    width: 170px;
    height: 30px;
    border: 1px solid #999;
    font-size: 18px;
    color: #1c87c9;
    background-color: #eee;
    border-radius: 5px;
    box-shadow: 4px 4px #ccc;

}

.close-button{
    border: none;
    display: inline-block;
    padding: 8px 16px;
    vertical-align: middle;
    overflow: hidden;
    text-decoration: none;
    color: inherit;
    background-color: inherit;
    text-align: center;
    cursor: pointer;
    white-space: nowrap;
    position: absolute;
    right: 310px;
    color: rgba(0, 126, 128, 1);
    top: 105px;
    font-weight: bold;
    font-size: 20px;
    outline: none;
}

.close-button:hover{
    color: rgb(2, 97, 99);

}
.streaming-button-container{
    margin: 10px;
}

.streaming-button{
   outline: none;
   border: none;
   background-color: inherit;
   color: #1b6f20;
   font-weight: bold;
   font-size: 20px;
   cursor: pointer;
   margin:5px;

}

.streaming-button:hover {
   color: #134916;

}

.search {
    -webkit-appearance: none;
    border: none;
    outline: none;
    padding: 10px;
    width: 50%;
    line-height:30px;
    margin-bottom:30px;
}

.searchButton{
width: 40px;
height: 36px;
border: 1px solid #00B4CC;
background: #00B4CC;
text-align: center;
color: #fff;
border-radius: 0 5px 5px 0;
cursor: pointer;
font-size: 20px;}



